<template>
  <div>
    <div class="create-task">
      <input
        type="text"
        id="create-task"
        v-model="newTask"
        placeholder="Add New Task"
      />
      <button v-on:click="addNewTask">Add</button>
    </div>
    <div class="container">
      <div
        class="item"
        v-for="(task, index) in tasks"
        :item="task"
        :index="index"
        :key="task._id"
        v-on:dblclick="deleteTask(task._id)"
      >
        <p class="content">{{ task.task }}</p>
        <p class="date">{{ task.dateCreated.substring(0, 10) }}</p>
      </div>
    </div>
    <div class="instruction">
      <p>
        Warning: this app might take a little to process! please take your time
      </p>
      <p>Delete: for delete the item please double click on items</p>
      <p>Scroll down to see remain task</p>
    </div>
  </div>
</template>

<script>
import Task from "../taskService";
const data = new Task();

export default {
  name: "taskComponent",
  data() {
    return {
      tasks: [],
      newTask: "",
    };
  },
  created() {
    data.getData().then((res) => {
      this.tasks = res.data;
    });
  },
  methods: {
    async addNewTask() {
      await data.insertData(this.newTask);
      this.newTask = "";
      await data.getData().then((res) => {
        this.tasks = res.data;
      });
    },
    async deleteTask(id) {
      await data.deleteTask(id);
      await data.getData().then((res) => {
        this.tasks = res.data;
      });
    },
  },
};
</script>

